body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  padding: 0 20px;
}

.content{
  padding: 20px 0;
  min-height: calc(100vh - 105px);
}

.productList{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
  gap:10px;
}

.regularPrice{
    text-decoration: line-through;
    color: black;
}

.finalPrice{
  font-size: 20px;
  font-weight: bold;
  color: red;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content{
  flex-grow: 1;
}

.cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;

  cursor: pointer;
  z-index: 5;
  padding: 1rem;
}
@media(min-width: 767px){
  .cart{
    top:5rem;
    bottom: unset;
  }
}

.basket-list{
  position: fixed;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);

  background-color: white;
  width: 50%;
  max-width: 95%;

  animation: show 500ms ease-in-out;
}

.basket-close-pointer{
  cursor: pointer;
}

@keyframes show{
  from{
    top:70%;
    opacity: 0;;
  }
  to{
    top:50%;
    opacity: 1;
  }
}

.basket-quantity{
  cursor: pointer;
  vertical-align: middle;
  color: #26a69a;
}

.submit-basket{
  position: absolute;
  right: 3%;
  bottom: 0.4rem;
  height: 2rem;
  padding: auto;
  align-items: left;
}

#toast-container{
  top: 3rem !important;
  animation: fade-in 500ms ease-in-out;
}
@keyframes fade-in{
  from{
    opacity: 0;
    transform: translateY(12rem);
  }
  to{
    opacity: 1;
    transform: translateY(0rem);
  }
}